section {
    position: relative;
}
section.bg {
    background-image: url(../pictures/docimage.jpg);
    height: 50vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 15cm;
  }
h1 {
    color: azure;
    font-size: 76px;
    line-height: 78px;
    font-weight: 400;
    margin-bottom: 34px;
}
p {
    color: white;
    font-size: 18px;
    line-height: 32px;
    letter-spacing: 0.02em;
    width: 70%;
    margin-bottom: 75px;
}
a.btn-primary {
    background: #fd9840;
    border-color: #fd9840;
    border-radius: 5px;
    font-size: 20px;
    font-weight: 400;
    line-height: 16px;
    color: #fefefe;
    padding: 24px 30px;
}